

import { put, take, call, fork } from 'redux-saga/effects';

import * as types from '../constants/ActionTypes';
import { GraphQLClient } from '../utils/GraphQLClient';
import { lottoNums } from '../utils/GraphQLQuery';
import { fetchLottoNums, receiveLottoNums } from '../actions/global';



export function* requestLottoNums(size, lottoId, playId) {
  try {
    console.log(size, lottoId, playId);
    const query = { size, lottoId, playId }
    const queryQql = lottoNums(query);
    console.log(queryQql);
    yield put(fetchLottoNums());
    const LottoNumsList = yield call(GraphQLClient, queryQql);
    yield put(receiveLottoNums(LottoNumsList));
  } catch (error) {
    yield put(receiveLottoNums({}));
    console.log(error);
  }
}

export function* watchRequestLottoNums() {
  while (true) {
    const {
      size, lottoId, playId
    } = yield take(types.REQUEST_LOTTONUMS);
    console.log(size, lottoId, playId);
    yield fork(requestLottoNums, size, lottoId, playId);
  }
}


